import React from 'react'
import abtm from "../images/abtm.jpeg"
import "./About.css"
function About() {
  return (
    <div id='about'>
        <div className='about-head'>About Me</div>
        <div className="about-info">
            <div className="about-image">
                <div className='abt-img-box'>
                <img src={abtm} alt="" />
                </div>
            </div>
            <div className="about-desc">
           <p className="about-p"><li>Hello, I'm Aniket Rajani, a recent Computer Engineering graduate from Pune University. I am enthusiastic about web development and data analysis.</li></p>
          
           <p className="about-p"><li>I'm committed to delivering excellence, thriving in collaborative environments, and eager to contribute to innovative projects.</li></p>
           
            <p className="about-p"><li> I'm driven by a passion for learning and dedicated to putting in the hard work necessary to meet deadlines.</li></p>
          
            <p className="about-p"><li>Let's connect and explore opportunities together!</li></p>
            </div>
        </div>
    </div>
  )
}

export default About