import React from 'react'
import "./Skills.css"
import { CircularProgressbarWithChildren } from 'react-circular-progressbar';
import VisibilitySensor from "react-visibility-sensor";
import 'react-circular-progressbar/dist/styles.css';
import h from "../images/html.png"
import cs from "../images/css-3.png"
import js from "../images/java-script.png"
import rj from "../images/physics.png"
import nj from "../images/node-js.png"
import p from "../images/python.png"
import c from "../images/c-logo.png"
import sql from "../images/database.png"
function Skills() {
  return (
      <div className="skills" id="skills">
  <div className='skhead'>Technical Skills</div>
  <div className='skhead1'><span style={{color:"#28BCC0"}}>Web</span> Development</div>

<div className="allskills">
  <div className="skillsn">
  <h2>HTML</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 85 : 0;
return(
               <CircularProgressbarWithChildren value={percentage}  ><img src={h}  className="cimg" style={{ width: '70px', height: '70px', }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>
<div className="skillsn">
  <h2>CSS</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 85 : 0;
return(
               <CircularProgressbarWithChildren value={percentage}><img src={cs} className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>
<div className="skillsn">
  <h2>JAVASCRIPT</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 60 : 0;
return(
               <CircularProgressbarWithChildren value={percentage}><img src={js} className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>
<div className="skillsn">
  <h2>REACT JS</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 70 : 0;
return(
               <CircularProgressbarWithChildren value={percentage}  ><img src={rj} className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>
<div className="skillsn">
  <h2>NODE JS</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 50 : 0;
return(
               <CircularProgressbarWithChildren value={percentage}  ><img src={nj} className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>

</div>
<div className='skhead1'><span style={{color:"#28BCC0"}}>Programming </span>Languages</div>
<div className="allskills">
<div className="skillsn">
  <h2>PYTHON</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 75 : 0;
return(
               <CircularProgressbarWithChildren value={percentage} ><img src={p} className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>
<div className="skillsn">
  <h2>C++</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 70 : 0;
return(
               <CircularProgressbarWithChildren value={percentage} ><img src={c} className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>
<div className="skillsn">
  <h2>SQL</h2>
  <VisibilitySensor>
            {({ isVisible }) => {const percentage = isVisible ? 70 : 0;
return(
               <CircularProgressbarWithChildren value={percentage} ><img src={sql}  className="cimg" style={{ width: '70px', height: '70px' }} alt="" /></CircularProgressbarWithChildren>);}}
               </VisibilitySensor>
</div>

</div>
</div>
  )
}

export default Skills